import React from "react";

function ProjectSpotlight() {
  return (
    <section className="project-spotlight navbar-spacing">
      <div className="row">
        <div className="col-lg-8"></div>

        <div className="col-lg-4">
          <div>
            <h2>PyQt Tetris</h2>
            <div>Some date/tag alignment</div>
            <hr />
            <h5>Synopsis</h5>
            <p>Description of what it is</p>
            <h5>How it works</h5>
            <p>Some more description</p>
            <h5>Challenge and Future work</h5>
            <h5>Share + Links</h5>
            <p>Some links</p>
          </div>
        </div>
      </div>
    </section>
  );
}

export default ProjectSpotlight;