import React from "react";
import { Switch, Route } from "react-router-dom";

import NavBar from "./general/NavBar.jsx";
import HomePage from "./home/HomePage.jsx";
import ProjectPage from "./projects/ProjectPage.jsx";
import WIPPage from "./wip/WIPPage.jsx";
import Footer from "./general/Footer.jsx";
import ProjectSpotlight from "./projects/ProjectSpotlight.jsx";

function App() {

  console.log(process.env.PUBLIC_URL);

  return (
    <div className="App">

        <NavBar/>

        <Switch>
          <Route path="/projects/pyqt-tetris" component={ProjectSpotlight} />
          <Route path="/projects" component={ProjectPage} />
          <Route path="/wip" component={WIPPage} />
          <Route path="/" component={HomePage} />
          
        </Switch>

        <Footer/>

    </div>
  );
}

export default App;